*{margin:0;padding:0;box-sizing:border-box}:root{font-family:'Poppins'}button{cursor:pointer}ul,ol{list-style:none}.upload{display:inline-block}.upload label{border:2px solid #234;color:#234;padding:0.2rem 0.3rem;border-radius:0.3rem;cursor:pointer;font-size:0.9rem}.upload label:hover{background-color:#234;color:#fefefe}.upload input{display:none}.table-container{padding:0 0.3rem}.table-container h2{font-weight:500}.table-container table{border-collapse:collapse;margin:0.5rem 0;font-size:0.9em;font-family:'Poppins';min-width:400px;box-shadow:0 0 20px rgba(0,0,0,0.15)}.table-container table th,.table-container table td{padding:0.5rem 0.8rem}.table-container table thead tr{background-color:#009879;color:#ffffff;text-align:left}.table-container table thead tr th{padding:0.5rem}.table-container table thead tr th:first-of-type{border-top-left-radius:0.5rem}.table-container table thead tr th:last-of-type{border-top-right-radius:0.5rem}.table-container table tbody tr{border-bottom:1px solid #dddddd}.table-container table tbody tr td:nth-of-type(6){min-width:6.75rem}.table-container table tbody tr:nth-of-type(even){background-color:#f3f3f3}.table-container table tbody tr:last-of-type{border-bottom:2px solid #009879}.table-container table tbody tr:hover{color:#009879}.table-empty{margin-top:0.8rem}.table-empty label{text-decoration:underline;cursor:pointer;color:#267dd4}.table-empty label:hover{color:#ff3232}.enrich{margin-left:0.4rem;display:inline-block}.enrich button{border-radius:0.3rem;padding:0.2rem 0.3rem;cursor:default}.enrich button:not(:disabled){color:#009879;border-style:solid;border-color:#009879;background-color:transparent;cursor:pointer}.enrich button:not(:disabled):hover{background-color:#009879;color:#fefefe}.user-msg{position:fixed;bottom:1rem;left:1rem;background-color:#111010;color:#fefefe;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;border-radius:5px;padding:5px 20px}.user-msg p{-webkit-user-select:none;-ms-user-select:none;user-se